import React from "react";
import "./styles.css";
import NumberEditor from "./NumberEditor";
import "bootstrap/dist/css/bootstrap.css";
import { ProgressBar } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

const VALUES = [
  ["Enhance to 5sg", [40932000, 23384000, 21697200]],
  ["Enhance to 5ss", [40932000, 14227600, 21697200]],
  ["Evolve to 5sb", [18336000, 11525600, 16686000]],
  ["5s Fuse", [540000, 1080000, 1080000]],

  ["Enhance to 4sg", [9432000, 10988800, 14418000]],
  ["Enhance to 4ss", [5574400, 9196400, 11790000]],
  ["Evolve to 4sb", [3118800, 7130800, 7830000]],
  ["4s Fuse", [360000, 720000, 720000]],

  ["Enhance to 3sg", [1621000, 1889200, 5508000]],
  ["Enhance 3ss", [728000, 1722000, 3942000]],
  ["Enhance to 3sb", [425400, 108000, 2304000]],
  ["3s Fuse", [180000, 360000, 360000]],

  ["2s Fuse", [252400, 108000, 1134000]],

  ["MLC's Talent", [70, 2700]],
  ["Roster Talent", [20, 20]],

  ["Coin (50m max)", [1, 1]],
  ["Already Obtained Points", [1, 1]],

  ["HOF to 5sg", [40932000, 27678000, 21697200]],
  ["HOF to 5ss", [40932000, 18692000, 21697200]],
  ["HOF to 5sb", [18336000, 9466000, 16686000]],
  ["HOF to 4sg", [9432000, 9120000, 14418000]],
  ["HOF to 4ss", [5574400, 4800000, 11790000]],
  ["HOF to 4sb", [3118800, 2400000, 7830000]],
  ["HOF to 3sg", [1621000, 2314000, 5508000]],
  ["HOF 3ss", [728000, 1277000, 3942000]],
  ["HOF to 3sb", [425400, 585700, 2304000]],
  ["HOF to 2sg", [1621000, 1191200, 5508000]],
  ["HOF 2ss", [728000, 629200, 3942000]],
  ["HOF to 2sb", [425400, 281000, 2304000]]
];

const MAX_SCORE = 167000000; //58500000; //48750000;

function numberWithCommas(x) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { easy: false };

    this.onChange = this.onChange.bind(this);
  }

  onChange(k, v) {
    this.setState({ [k]: v });
  }

  render() {
    let editors = [];
    let total = 0;
    VALUES.forEach((element) => {
      let key = element[0];
      total +=
        parseInt(this.state[key] || 0, 10) *
        element[1][this.state["easy"] ? 0 : 1];
      editors.push(
        <NumberEditor
          label={key}
          key={key}
          pointValue={numberWithCommas(element[1][this.state["easy"] ? 0 : 1])}
          value={this.state[element[0]] || 0}
          onChange={(v) => this.onChange(element[0], v)}
        />
      );
    });
    return (
      <div className="App container">
        <div class="jumbotron">
          <h3>
            {/* <div class="col-xs-3">
                  <img
                    width="50px"
                    height="50px"
                    alt="WWE Champions"
                    src="https://uploads.codesandbox.io/uploads/user/3a5ded9e-287e-4fbf-a27f-2ddb02fe79f5/gghB-wwe_champions.png"
                  />
                </div> */}
            <div class="text-center">WWE Champions Talent Up Calculator</div>
          </h3>
          <h4>Christmas Conspiracy</h4>
        </div>

        <div className="container">{editors}</div>
        <div className="card mt-4">
          <ProgressBar now={(total / MAX_SCORE) * 100} />
          <div>
            {" "}
            Total: {numberWithCommas(total)} / {numberWithCommas(MAX_SCORE)}
            <i>
              {total !== 0 && total < MAX_SCORE
                ? " need " + numberWithCommas(MAX_SCORE - total)
                : ""}
            </i>
          </div>
          <i>
            {total > MAX_SCORE
              ? total > MAX_SCORE * 1.5
                ? "Easy there, shamu"
                : "Too sweet!"
              : total > MAX_SCORE * 0.5
              ? "getting there"
              : total > 0
              ? "What else you got?"
              : ""}
          </i>
        </div>
        <div className="small">
          Copyright Â© 2021 Fuzzy#3944 Forked by JCongDerulo#5058
        </div>
        <div class="col-xs-3">
          {/* <a href="https://wwechampions.com/faction/Austins%20Theory">
            <img
              width="100px"
              height="100px"
              alt="Austin's Theory"
              src="https://uploads.codesandbox.io/uploads/user/3a5ded9e-287e-4fbf-a27f-2ddb02fe79f5/HPyB-austins_theory.png"
            />
          </a> */}
        </div>
      </div>
    );
  }
}
